@font-face {
  font-family: "Revue";
  src: url("../fonts/Revue.eot");
  src: url("../fonts/Revue.eot?#iefix") format("embedded-opentype"),
    url("../fonts/Revue.woff2") format("woff2"),
    url("../fonts/Revue.woff") format("woff"),
    url("../fonts/Revue.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

